import React from 'react';
import { FieldOption } from 'view/form/formContracts';
import { IFormValues } from '../../view/form/FormContainer';
import { UseFormRegister } from 'react-hook-form';

type FileInputProps = {
  name: FieldOption;
  register: UseFormRegister<IFormValues>;
  type: string;
};

const FileInput = ({ name, type, register }: FileInputProps) => {
  return (
    <>
      <input
        {...register(name)}
        role={type}
        className={'form-input'}
        id={name}
        type={type}
        name={name}
      />
    </>
  );
};

export default FileInput;
