import React, { useCallback } from 'react';
import GLobalState from 'context/globalState/GLobalState';
import getInitBlobalState from './getInitGlobalState';
import { IGame } from 'api/gameContracts';
import useGlReducer, { globalStateActions } from './glboalStateReduecer';
import { ICard } from 'view/form/formContracts';

const GlobalStateProvider = ({ children }: { children: React.ReactNode }) => {
  getInitBlobalState();
  const [globalState, dispatch] = useGlReducer(getInitBlobalState());

  const updateGlobalStateMovies = useCallback(
    (games: IGame[] | null) => {
      dispatch({ type: globalStateActions.updateGames, payload: games });
    },
    [dispatch]
  );

  const updateGlobalStateCards = useCallback(
    (card: ICard | null) => {
      let cards: ICard[] = [];
      if (globalState.cards && card) {
        cards = [...globalState.cards, card];
      } else if (card) {
        cards = [card];
      }
      dispatch({ type: globalStateActions.updateCards, payload: cards });
    },
    [dispatch, globalState.cards]
  );

  const gl = { globalState, updateGlobalStateMovies, updateGlobalStateCards };

  return <GLobalState.Provider value={gl}>{children}</GLobalState.Provider>;
};

export default GlobalStateProvider;
