import * as yup from 'yup';

const getMaxDate = () => {
  return new Date().toLocaleDateString();
};

const getMinDate = () => {
  return new Date('1900/01/01').toLocaleDateString();
};

const fileFormats = ['image/jpg', 'image/jpeg', 'image/gif', 'image/png'];

const schema = yup
  .object()
  .shape({
    notifications: yup.boolean().notRequired(),
    name: yup.string().required('Enter name'),
    agreed: yup.boolean().isTrue('You have to agreed to continue'),
    birthday: yup.date().min(getMinDate()).max(getMaxDate()).typeError('please enter a valid date'),
    avatar: yup
      .mixed()
      .test('required', 'Please select a file', (value) => value && value.length)
      .test('fileFormat', 'Unsupported Format', (value) => {
        if (!value || value.length === 0) {
          return false;
        }
        return fileFormats.includes(value[0].type);
      }),
  })
  .required();

export default schema;
