import React from 'react';
import DescItem from '../pageMovie/DescreptionItem';
import { Button } from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import './index.css';
import E404 from 'components/E404';
import { IGame } from 'api/gameContracts';

const GamePagePresentation = ({ game }: { game: IGame | undefined }) => {
  return (
    <>
      {game ? (
        <div className="container page__wrapper">
          <h1 className="page__title title">{game.name}</h1>
          <div className="page">
            <div className="page__image">
              <img className="page-img" src={game.background_image} alt={game.name} />
            </div>
            <div className="page__descreption">
              <DescItem title="Genre: " content={game.genres[0].name} />
              <DescItem title="Critic score: " content={game.metacritic} />
              <DescItem title="User rating: " content={game.rating} />
              <DescItem title="Released: " content={game.released} />
            </div>
          </div>
          <Button className="page__btn" variant="outlined">
            <RouterLink to="/">Return to catalog</RouterLink>
          </Button>
        </div>
      ) : (
        <E404 />
      )}
    </>
  );
};

export default GamePagePresentation;
