import React, { ChangeEvent, useCallback, useContext, useEffect, useMemo, useState } from 'react';
import GamesCatalogPresentation from './GamesCatalogPresentation';
import Search from 'components/search';
import Pagination from 'components/pagination/';
import SortMenu from 'components/sortMenu';
import BackDrop from 'components/backdrop';
import WithRouter from 'components/highOrderComponents/WithRouter';
import { WithRouterProps } from 'router/routerContracts';
import GLobalState from 'context/globalState/GLobalState';
import ErrorsContext from 'context/errors/ErrorsContext';
import useFetchData from 'hooks/useFetchData';
import LocalStore from 'localStorage';
import { IGameResponse, IOrderOption } from 'api/gameContracts';
import { getGamesList } from 'api/gameApi';
import _ from 'lodash';
import './index.css';

type GamesCatalogContainerProps = Record<string, string>;

const GameCatalogContainer = ({ router }: WithRouterProps) => {
  const localStore = useMemo(() => new LocalStore(), []);
  const initialSearchValue = localStore.getValue('gameLand') ?? 'The lord of the rings';
  const [searchValue, setSearchValue] = useState(initialSearchValue);
  const [page, setPage] = useState(1);
  const [amountOfPages, setAmountOfPages] = useState(1);
  const [orderOption, setOrderOption] = useState<IOrderOption>('rating');

  const onCardClick = (id: number) => {
    router.navigate(`/game/${id}`);
  };

  const onSearchChange = (value: string) => {
    setSearchValue(value);
  };
  const onSearchChangeLazy = _.debounce(onSearchChange, 600);

  const onPageChange = (event: ChangeEvent<unknown>, page: number) => {
    setPage(page);
  };

  const onSortMenuOptionClick = (value: IOrderOption) => setOrderOption(value);

  const { updateErrorsList } = useContext(ErrorsContext);

  const { updateGlobalStateMovies } = useContext(GLobalState);

  const getFetchData = useCallback(
    () => getGamesList(searchValue, page, orderOption),
    [orderOption, page, searchValue]
  );

  const { isLoading, data } = useFetchData<IGameResponse>(getFetchData, updateErrorsList);
  useEffect(() => updateGlobalStateMovies(data?.results ?? null), [data, updateGlobalStateMovies]);

  useEffect(() => {
    if (data) {
      const count = data.count < 30 ? data?.count : 30;
      setAmountOfPages(count);
    }
  }, [data]);

  useEffect(() => {
    localStore.updateValue(searchValue, 'gameLand');
  }, [searchValue, localStore]);

  return (
    <main>
      <div className="container">
        <h1 className="catalog__title">GameLand</h1>
        <Search onChange={onSearchChangeLazy} value={searchValue} />
        <SortMenu onSortMenuOptionClick={onSortMenuOptionClick} />
        {isLoading ? (
          <BackDrop open={true} />
        ) : (
          <>
            <GamesCatalogPresentation games={data?.results ?? null} onCardClick={onCardClick} />
            <Pagination onPageChange={onPageChange} page={page} count={amountOfPages} />
          </>
        )}
      </div>
    </main>
  );
};

const GamesCatalogWithRouter = WithRouter<GamesCatalogContainerProps>(GameCatalogContainer);

export default GamesCatalogWithRouter;
