import React from 'react';
import { ICard } from 'view/form/formContracts';

import './index.css';

export default function FormCard({ card }: { card: ICard }) {
  return (
    <section role={card.name} className="form-card">
      <img
        className="form-card__img"
        src={window.URL.createObjectURL(card.avatar)}
        alt={card.name}
      />
      <div className="form-card__text-content">
        <p className="text-contnent__item p-20">Name is: {card.name}</p>
        <p className="text-contnent__item p-20">Birthday: {card.birthday?.toLocaleDateString()}</p>
        <p className="text-contnent__item p-20">Language: {card.language}</p>
        <p className="text-contnent__item p-20">
          Notifications are: {card.notifications ? 'on' : 'off'}
        </p>
        <p className="text-contnent__item p-20">
          Agreed to share personal information: {card.agreed ? 'yes' : 'no'}
        </p>
      </div>
    </section>
  );
}
