import React from 'react';
import ErrMsg from 'components/errMessage/testErrMsg';
import { FieldOption } from 'view/form/formContracts';

type FormControlProps = {
  errMsg: string;
  id: FieldOption;
  label: string;
  children: React.ReactNode;
};

const FormControl = ({ id, label, children, errMsg }: FormControlProps) => {
  const agreed = id === 'agreed' ? 'agreed' : '';
  return (
    <div className={` ${agreed} form-item`}>
      <label htmlFor={id}>{label}</label>
      {children}
      <ErrMsg errMsg={errMsg} />
    </div>
  );
};

export default FormControl;
