import { Reducer, useReducer } from 'react';

export type FetchData<DataT> = {
  data: DataT | null;
  error: Error | null;
  isLoading: boolean;
};

export enum actionKind {
  fetch = 'FETCH',
  reslove = 'RESOLVE',
  reject = 'REJECT',
}

export type Event<DataT, ErrorT = Error> =
  | { type: actionKind.fetch; payload: null }
  | { type: actionKind.reslove; payload: DataT }
  | { type: actionKind.reject; payload: ErrorT };

const fetchReducer = <DataT>(state: FetchData<DataT>, { type, payload }: Event<DataT>) => {
  switch (type) {
    case actionKind.fetch:
      return {
        ...state,
        isLoading: true,
      };
    case actionKind.reslove:
      return {
        data: payload,
        error: null,
        isLoading: false,
      };
    case actionKind.reject:
      return {
        error: payload,
        data: null,
        isLoading: false,
      };
    default:
      throw new Error(`Unknown action type: ${type}`);
  }
};

const useFetchReducer = <DataT>(initValue: FetchData<DataT>) =>
  useReducer<Reducer<FetchData<DataT>, Event<DataT>>>(fetchReducer, initValue);

export default useFetchReducer;
