import { useEffect, useRef } from 'react';

import useFetchReducer, { actionKind, FetchData } from './useFetchReducer';
import getInitData from './getInitData';

const useFetchData = <DataT,>(
  getData: () => Promise<DataT>,
  updateErrorsList: (err: Error) => void
): FetchData<DataT> => {
  const [data, dispatch] = useFetchReducer<DataT>(getInitData());
  const onError = useRef(updateErrorsList);

  useEffect(() => {
    dispatch({ type: actionKind.fetch, payload: null });
    getData()
      .then((response) => {
        dispatch({ type: actionKind.reslove, payload: response });
      })
      .catch((error: Error) => {
        dispatch({ type: actionKind.reject, payload: error });
        onError.current(error);
      });
  }, [dispatch, getData]);
  return { ...data };
};

export default useFetchData;
