import React, { FormEvent } from 'react';
import FormCards from 'components/formCards';
import FormControl from 'components/formControl';
import { DateInput, FileInput, Input, SelectInput, SwitchInput } from 'components/input';
import { FieldErrorsImpl, UseFormRegister } from 'react-hook-form';
import { Button } from '@mui/material';
import { IFormValues } from './FormContainer';

import './index.css';

type FormPresentProps = {
  register: UseFormRegister<IFormValues>;
  handleFirstInputType: () => void;
  handleSubmit: (e?: FormEvent) => Promise<void>;
  firstType: boolean;
  errors: Partial<FieldErrorsImpl<IFormValues>>;
  onResetCards: () => void;
};

const FormPresent = ({
  handleSubmit,
  handleFirstInputType,
  firstType,
  errors,
  register,
  onResetCards,
}: FormPresentProps) => {
  return (
    <div className="form-wrapper">
      <div className="container">
        <form className="form" onSubmit={handleSubmit} onChange={handleFirstInputType}>
          <FormControl label="Name" id="name" errMsg={errors.name?.message ?? ''}>
            <Input register={register} name="name" placeholder="Enter your name" type="text" />
          </FormControl>
          <FormControl label="Birthday" id="birthday" errMsg={errors.birthday?.message ?? ''}>
            <DateInput register={register} name="birthday" type="date" />
          </FormControl>
          <FormControl label="Avatar" id="avatar" errMsg={errors.avatar?.message ?? ''}>
            <FileInput register={register} name="avatar" type="file" />
          </FormControl>
          <FormControl label="Language" id="language" errMsg={errors.language?.message ?? ''}>
            <SelectInput
              register={register}
              name="language"
              options={[
                { value: 'ru', optionInnerText: 'Russian' },
                { value: 'en', optionInnerText: 'English' },
              ]}
            />
          </FormControl>
          <FormControl
            label="Notifications"
            id="notifications"
            errMsg={errors.notifications?.message ?? ''}
          >
            <SwitchInput register={register} name="notifications" type="checkbox" />
          </FormControl>
          <FormControl label="Agreed" id="agreed" errMsg={errors.agreed?.message ?? ''}>
            <Input
              register={register}
              name="agreed"
              placeholder="Are you agreed?"
              type="checkbox"
            />
          </FormControl>
          <Button disabled={firstType} type="submit" variant="outlined">
            Submit
          </Button>
        </form>
      </div>
      <FormCards />
      <Button className="resetBtn" onClick={onResetCards}>
        Reset Cards
      </Button>
    </div>
  );
};

export default FormPresent;
