import React, { useContext, useEffect, useState } from 'react';
import FormPresent from './FormPresentation';
import { useForm } from 'react-hook-form';

import { yupResolver } from '@hookform/resolvers/yup';
import schema from './yupSchema';
import getFormDefaultValues from './getFormDefaultValues';

import convert2Blob from 'components/formCard/covert2Blob';

import './index.css';
import GLobalState from 'context/globalState/GLobalState';

export type IFormValues = {
  name: string;
  agreed: boolean;
  avatar: FileList | null;
  birthday: Date | null;
  language: string;
  notifications: boolean;
};

const FormContainer = () => {
  const { formState, handleSubmit, reset, register } = useForm<IFormValues>({
    resolver: yupResolver(schema),
  });

  const { errors, isSubmitSuccessful } = formState;

  const [firstType, setFirstType] = useState(true);

  const { updateGlobalStateCards } = useContext(GLobalState);

  const handleFirstInputType = () => {
    if (!firstType) {
      return;
    }
    setFirstType(false);
  };

  useEffect(() => {
    if (isSubmitSuccessful) {
      reset(getFormDefaultValues());
    }
  }, [isSubmitSuccessful, reset]);

  const onSubmit = (data: IFormValues) => {
    const file = convert2Blob(data.avatar);
    const newCard = { ...data, avatar: file };
    updateGlobalStateCards(newCard);
  };

  const onResetCards = () => updateGlobalStateCards(null);

  return (
    <FormPresent
      register={register}
      handleSubmit={handleSubmit(onSubmit)}
      handleFirstInputType={handleFirstInputType}
      firstType={firstType}
      errors={errors}
      onResetCards={onResetCards}
    />
  );
};

export default FormContainer;
