import React from 'react';
import { Button } from '@mui/material';

import './index.css';
import { IGame } from '../../api/gameContracts';

export type GameCardProps = {
  game: IGame;
  onCardClick: (id: number) => void;
};

const GameCard = ({ game, onCardClick }: GameCardProps) => {
  return (
    <div data-testid="gameCard" className="gameCard" onClick={() => onCardClick(game.id)}>
      <img className="gameCard__image" src={game.background_image} alt={game.name} />
      <h1 className="gameCard__title">{game.name}</h1>
      <div className="gameCard__descrep">{game.released}</div>
      <Button variant="outlined">Read more</Button>
    </div>
  );
};

export default GameCard;
