import React from 'react';
import { FieldOption } from 'view/form/formContracts';
import { IFormValues } from '../../view/form/FormContainer';
import { UseFormRegister } from 'react-hook-form';

export type SelectOption = {
  value: string;
  optionInnerText: string;
};

type SelectInputProps = {
  register: UseFormRegister<IFormValues>;
  name: FieldOption;
  options: SelectOption[];
};

const SelectInput = ({ name, options, register }: SelectInputProps) => {
  return (
    <>
      <select {...register(name)} className={'form-input'} defaultValue="en" name={name} id={name}>
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.optionInnerText}
          </option>
        ))}
      </select>
    </>
  );
};

export default SelectInput;
