import React from 'react';
import { FieldOption } from 'view/form/formContracts';
import { IFormValues } from '../../view/form/FormContainer';
import { UseFormRegister } from 'react-hook-form';

type InputProps = {
  register: UseFormRegister<IFormValues>;
  name: FieldOption;
  type: string;
  placeholder: string;
};

const Input = ({ name, register, type }: InputProps) => {
  return (
    <>
      <input {...register(name)} id={name} type={type} role={type} className={'form-input'} />
    </>
  );
};

export default Input;
