import React from 'react';
import { FieldOption } from 'view/form/formContracts';
import { IFormValues } from '../../view/form/FormContainer';
import { UseFormRegister } from 'react-hook-form';

type DateInputProps = {
  name: FieldOption;
  type: string;
  register: UseFormRegister<IFormValues>;
};

const DateInput = ({ name, type, register }: DateInputProps) => {
  return (
    <>
      <input
        {...register(name)}
        role={type}
        className={'form-input'}
        id={name}
        name={name}
        type={type}
      />
    </>
  );
};

export default DateInput;
