import React, { useContext } from 'react';
import PagePresent from './PageGamePresentation';

import withRouter from 'components/highOrderComponents/WithRouter';
import { WithRouterProps } from 'router/routerContracts';
import GLobalState from 'context/globalState/GLobalState';

type PageProps = Record<string, string>;
const MoviePageContainer = ({ router }: WithRouterProps) => {
  const { id } = router.params;
  const { globalState } = useContext(GLobalState);

  const game = globalState?.games?.filter((game) => game.id === +(id as string))[0];

  return <PagePresent game={game} />;
};

export default withRouter<PageProps>(MoviePageContainer);
