import { IGame } from 'api/gameContracts';
import { Reducer, useReducer } from 'react';
import { ICard } from 'view/form/formContracts';

export enum globalStateActions {
  updateGames = 'updateGames',
  updateCards = 'updateCards',
}

export type IGloBalStateData = {
  games: IGame[] | null;
  cards: ICard[] | null;
};

type GlobalStateEvent =
  | { type: globalStateActions.updateGames; payload: IGame[] | null }
  | { type: globalStateActions.updateCards; payload: ICard[] | null };

const useGlReducer = (initialState: IGloBalStateData) => {
  const reducer = (state: IGloBalStateData, { type, payload }: GlobalStateEvent) => {
    switch (type) {
      case globalStateActions.updateGames:
        return {
          ...state,
          games: payload,
        };
      case globalStateActions.updateCards:
        return {
          ...state,
          cards: payload,
        };
      default:
        throw new Error(`Unknown action type: ${type}`);
    }
  };

  return useReducer<Reducer<IGloBalStateData, GlobalStateEvent>>(reducer, initialState);
};

export default useGlReducer;
