import React, { useContext } from 'react';
import FormCard from 'components/formCard/FormCard';

import './index.css';
import GLobalState from 'context/globalState/GLobalState';

const FormCards = () => {
  const { globalState } = useContext(GLobalState);
  return (
    <div className={globalState?.cards?.length ? 'form-cards-wrapper' : ''}>
      <div className="container">
        <div className="form-cards">
          {globalState?.cards?.map((card) => (
            <FormCard key={card.name} card={card} />
          ))}
        </div>
      </div>
    </div>
  );
};

export default React.memo(FormCards);
