import React from 'react';
import { FieldOption } from 'view/form/formContracts';

import './switch.css';
import { IFormValues } from '../../view/form/FormContainer';
import { UseFormRegister } from 'react-hook-form';

type SwitchProps = {
  register: UseFormRegister<IFormValues>;
  name: FieldOption;
  type: string;
};

const SwitchInput = ({ name, type, register }: SwitchProps) => {
  return (
    <div className="switch">
      <input {...register(name)} className="toggle-button" name={name} type={type} id={name} />
    </div>
  );
};

export default SwitchInput;
