import React, { useCallback, useState } from 'react';
import ErrorsContext from '../../context/errors/ErrorsContext';

const ErrorContextProvider = ({ children }: { children: React.ReactNode }) => {
  const [errors, setErrors] = useState<Error[]>([]);

  const updateErrorsList = useCallback((err: Error) => setErrors([...errors, err]), [errors]);

  const onErrorClick = useCallback(
    (err: Error) => {
      const filteredErrors = errors.filter((error) => error !== err);
      setErrors(filteredErrors);
    },
    [errors]
  );

  return (
    <ErrorsContext.Provider value={{ errors, updateErrorsList, onErrorClick }}>
      {children}
    </ErrorsContext.Provider>
  );
};

export default ErrorContextProvider;
