import React from 'react';
import GameCard from '../gameCard';
import { IGame } from 'api/gameContracts';

type GamesCatalogPresentationProps = {
  games: IGame[] | null;
  onCardClick: (id: number) => void;
};

const GamesCatalogPresentation = ({ games, onCardClick }: GamesCatalogPresentationProps) => {
  return (
    <div className="container">
      <div data-testid="gamesCatalog" className="games-catalog">
        {games ? (
          games.map((game) => <GameCard onCardClick={onCardClick} key={game.id} game={game} />)
        ) : (
          <h1 className="error__title">Nothing found</h1>
        )}
      </div>
    </div>
  );
};

export default GamesCatalogPresentation;
