import * as React from 'react';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { IOrderOption } from 'api/gameContracts';

export default function BasicMenu({
  onSortMenuOptionClick,
}: {
  onSortMenuOptionClick: (value: IOrderOption) => void;
}) {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = (value: IOrderOption) => {
    setAnchorEl(null);
    onSortMenuOptionClick(value);
  };

  return (
    <div>
      <Button
        id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        Sort
      </Button>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        <MenuItem onClick={() => handleClose('name')}>Name</MenuItem>
        <MenuItem onClick={() => handleClose('released')}>Released</MenuItem>
        <MenuItem onClick={() => handleClose('rating')}>Rating</MenuItem>
        <MenuItem onClick={() => handleClose('metacritic')}>Metacritic</MenuItem>
        <MenuItem onClick={() => handleClose('added')}>Added</MenuItem>
        <MenuItem onClick={() => handleClose('created')}>Created</MenuItem>
        <MenuItem onClick={() => handleClose('updated')}>Updated</MenuItem>
      </Menu>
    </div>
  );
}
