import { IGameDetail, IGameResponse, IOrderOption } from './gameContracts';
import makeReauest from './makeReauest';

//const API_KEY = 'e60b14b4174044d9880190c10c13d738'; // apiKey1
const API_KEY = '3303b03c9f754dbdb8e22a0a43f7abd1'; // apiKey2
const BASE_URL = `https://api.rawg.io/api/games`;

export const getGamesList = async (
  search = '',
  page = 1,
  orderBy: IOrderOption = '',
  limit = 9
): Promise<IGameResponse> => {
  let url = `${BASE_URL}?key=${API_KEY}&page=${page}&page_size=${limit}`;
  if (search !== '' || search) {
    url += `&search=${search}&ordering=${orderBy}`;
  }
  return await makeReauest<IGameResponse>(url);
};

export const getGame = async (id: number): Promise<IGameDetail> => {
  if (!id) {
    throw new Error('Id must be provided');
  }
  return makeReauest<IGameDetail>(`${BASE_URL}/${id}?key=${API_KEY}`);
};
